import React from 'react';
import style from './Paginator.module.css';
import Pagination from "react-js-pagination";

const Paginator = (props) => {
  let { usersTotalCount, pageCount, activePage, onPageChange } = props;
  
  let pages = [];
  for (let i = 1; i <= pageCount; i++) {
    pages.push(i);
  }
  
  return (
    <div>
      <Pagination 
        activePage={activePage} 
        itemsCountPerPage={10}
        totalItemsCount={usersTotalCount}
        pageRangeDisplayed={10}
        onChange={onPageChange}
        prevPageText="Prev page"
        nextPageText="Next page"
        firstPageText="First page"
        lastPageText="Last page"
        innerClass={style.inner}
        itemClass={style.item}
        linkClass={style.link}
        activeClass = {style.itemActive}
        activeLinkClass= {style.linkActive}
        />
    </div>


    // <>
    //   <div>{usersTotalCount === 0 ? null : `Всего пользователей: ${usersTotalCount}`}</div>
    //   <div>{pageCount === 0 ? null : `Всего страниц: ${pageCount}`}</div>

    //   <div className={style.pageNumConteiner}>
    //     {
    //       pages.map(p => {
    //         return <span key={p} 
    //                      className={`${style.pageItem} ${currentPage === p ? style.currentPage : ""} `}
    //                      onClick={(e) => { onPageChange(p) }}>{p}
    //                </span>
    //       })
    //     }
    //   </div>
    // </>
  )
};

export default Paginator;